body {
  font-family: "Oswald", "Arial", sans-serif;
  color: #111111;
  min-width: 320px;
  width: 100%;
  padding: 0;
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  padding: 10px;
  background-color: #68b738;
  display: block;
  box-sizing: border-box;
  width: 280px;
  margin: 0;
  text-align: center;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: #8fc46e;
}
.page-header {
  width: 100%;
  background-color: #ffffff;
}
.page-header__wrapper {
  padding: 15px 20px 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 320px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}
.page-header__logo {
  width: 195px;
  margin-bottom: 15px;
}
.page-header__logo:hover,
.page-header__logo:focus {
