}
@media (min-width: 768px) {
  .page-header__wrapper {
    flex-direction: row;
    padding: 0 30px;
    width: 768px;
  }
  .page-header__logo {
    width: 177px;
    margin-bottom: 15px;
    margin-top: 8px;
  }
}
@media (min-width: 1300px) {
  .page-header__wrapper {
    padding: 0 40px;
    width: 1300px;
  }
  .page-header__wrapper--inverted {
    background: url("../img/index-background-desktop@1x.jpg") no-repeat top -48px left 112%;
  }
  .page-header__logo {
    width: 203px;
    margin-bottom: 15px;
    margin-top: 8px;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .page-header__wrapper--inverted {
    background: url("../img/index-background-desktop@2x.jpg") no-repeat top -48px left 112%;
    background-size: 720px auto;
  }
}
.main-nav {
  text-align: center;
  margin: 0 auto;
}
.main-nav__toggle {
  position: absolute;
  top: 28px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 22px;
  height: 22px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  left: 0;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__item {
  font-size: 20px;
  line-height: 20px;
  position: relative;
  text-transform: uppercase;
  color: #111111;
  text-decoration: none;
  display: block;
  padding-top: 23px;
  padding-bottom: 23px;
  font-weight: 500;
}
.main-nav__item::before {
  content: "";
  position: absolute;
  right: 0;
  top: -1px;
  left: -70px;
  width: 320px;
  height: 1px;
  background: #e6e6e6;
}
.main-nav__item-link {
  text-decoration: none;
  color: #111111;
}
.main-nav__item-link:hover,
.main-nav__item-link:focus {
  opacity: 0.6;
}
.main-nav__item-link:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__item {
  display: none;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    width: auto;
    margin-right: 0;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav--closed .main-nav__item {
    display: block;
  }
  .main-nav__item {
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
    font-weight: 400;
    padding-top: 26px;
    margin-right: 34px;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 14px;
    left: 0;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 1300px) {
  .main-nav__item {
    font-size: 20px;
    line-height: 25px;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 12px;
    left: 2px;
    height: 2px;
    background: #68b738;
  }
  .main-nav__item--inverted {
    position: relative;
  }
  .main-nav__item--inverted::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 12px;
    left: 2px;
    height: 2px;
    background: #ffffff;
  }
  .main-nav__item-link--inverted {
    color: #ffffff;
  }
}
