  .extra__item:nth-child(2n) {
    margin-left: 0;
  }
  .extra__item label {
    position: relative;
    font-size: 20px;
    line-height: 20px;
  }
  .extra__item-input + label::before {
    content: "";
    position: absolute;
    top: -5px;
    left: -60px;
    width: 38px;
    height: 38px;
    border: 1px solid #e6e6e6;
  }
  .extra__item-input:checked + label::after {
    content: "";
    position: absolute;
    top: 8px;
    left: -50px;
    width: 21px;
    height: 16px;
    background: url("../img/icon-tick.svg") no-repeat 0 0;
  }
}
